<template>
  <div>
    <button v-on:click.prevent="load" v-if="!isActive">Load Observation</button>
    <button class="positive" v-on:click.prevent="goTo" v-if="isActive">Active Observation</button>
    <error-list v-bind:errors="errors"></error-list>
  </div>
</template>

<script>
  import db from '../helpers/db'
  import ErrorList from './ErrorList'
  import router from '../router/index'


  export default {
    props: ['id'],
    components: {
      ErrorList
    },
    data () {
      return {
        errors: []
      }
    },
    computed: {
      title: function(){
        return this.$store.state.observation.id == this.id ? 'Active Observation' : 'Load Observation';
      },
      isActive: function(){
        return this.$store.state.observation.id == this.id;
      }
    },
    methods: {
      goTo () {
        router.push({ name: 'map' })
      },
      load () {
        db.observation(this.id, this.$store.state.auth_token).then((data) => {
          if (!data.activities.length) {
            alert('Could not activate this observation. Activities are empty.')
            return;
          }
          if (!data.quiz.quiz_questions.length) {
            alert('Could not activate this observation. Quiz is empty.')
            return;
          }
          this.$store.dispatch('setObservation', data);
        });
      }
    }
  }
</script>



