<template>
  <router-link v-if="observation" class="active-observation" :to="{ name: 'map' }">
    <p class="caption">Active observation</p>
    <h2>
      {{observation.name}}
    </h2>
  </router-link>
</template>

<style>
  .active-observation {
    background-color: #f4f4f4;
    padding:2rem 9rem 2rem 2rem;
    margin:2rem 0 0;
    position: relative;
    display: block;
  }
  .active-observation:after {
    content: '';
    position: absolute;
    right: 2rem;
    font-size: 5rem;
    line-height: 1;
    top: 50%;
    margin-top:-2.5rem;
    color: #2e9dfd;
  }

  .active-observation .caption {
    margin: 0;
    padding: 0;
    line-height: 1;
  }
  .active-observation h2 {
    margin: 0;
    padding: 0;
    display: inline;
    color: #2e9dfd;
    border-bottom:1px solid currentColor;
  }

</style>

<script>
  export default {
    computed: {
      observation () {
        return this.$store.state.observation;
      }
    }
  }
</script>



