<template>
  <div class="flex-container observation">

  <!-- router-link :to="{ name: 'map', params: { id: observation.id }}" class="observation" -->
    <div class="flex-item date"><p class="caption">{{date}}</p></div>
    <div class="flex-item flex-item_full info">
      <h4>{{observation.name}} <em v-if="observation.shop.name"></em></h4>
      <p class="caption" v-if="observation.shop.address">{{observation.shop.name}}, {{observation.shop.address}}</p>
      <p v-if="observation.description">{{observation.description}}</p>
    </div>
    <div class="flex-item">
      <button-load-observation v-bind:id="observation.id"></button-load-observation>
    </div>
  <!-- /router-link -->
  </div>
</template>

<style>
  .flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    align-items: flex-start;
  }

  .flex-item {
    order: 0;
    flex: 0 1 auto;
    align-self: auto;
    padding-right:1rem;
  }
  .flex-item:last-child {
    padding-right:0;
  }

  .flex-item_full {
    order: 0;
    flex: 1 1 auto;
    align-self: auto;
  }

  .observation {
    padding:2rem 0;
    border-top:1px solid #f4f4f4;
  }


  .observation .date p {
    margin: .5rem 0 0;
  }
  .observation .info {

  }
  .observation .info h4 {
    margin: 0;
  }
  .observation .info p {
    margin: .5rem 0 0 0;
  }
</style>

<script>
  import ButtonLoadObservation from './ButtonLoadObservation'

  export default {
    props: ['observation'],
    components: {
      ButtonLoadObservation
    },
    computed: {
      'date': function(){
        const d = new Date(this.observation.planned_date);
        return (d.getMonth()+1) + '/' + d.getDate() + '/' + d.getFullYear()
      }
    }
  }
</script>



