import Vue from 'vue'

const baseUrl = 'http://tracker.cloudcore.no/api';

const login = function login (username, password) {
  return Vue.http.get(baseUrl+'/login', {
    params: {
      email: username,
      password: password
    }
  })
    .then((response) => Promise.resolve(response.data))
    .catch((error) => Promise.reject(error));
}

const observations = function observations (auth_token) {
  return Vue.http.get(baseUrl+'/observations', {
    params: {
      auth_token: auth_token
    }
  })
    .then((response) => Promise.resolve(response.data))
    .catch((error) => Promise.reject(error));

}

const observation = function observation (id, auth_token) {
  return Vue.http.get(baseUrl+'/observations/' + id, {
    params: {
      auth_token: auth_token
    }
  })
    .then((response) => Promise.resolve(response.data))
    .then((observation) => {
      return map(id, auth_token).then((map) => {
        observation.map = map;
        return observation;
      })
    })
    .catch((error) => Promise.reject(error));
}

const map = function map (id, auth_token) {
  return Vue.http.get(baseUrl+'/observations/' + id + '/map', {
    params: {
      auth_token: auth_token
    }
  })
    .then((response) => Promise.resolve(response.data))
    .catch((error) => Promise.reject(error));
}

export default { login, observations, observation }



