<template>
  <div class="quiz" v-bind:class="{'quiz_visible': showQuiz}">
    <form class="form">
      <div class="clearfix" v-for="question in quiz.quiz_questions">
        <label>{{question.name}}</label>
        <select v-if="question.question_type === 'options only'">
          <option v-for="option in question.quiz_question_options" v-bind:value="option.quiz_question_id">
            {{ option.option }}
          </option>
        </select>
        <input v-if="question.question_type === 'custom float only'" type="number"></input>
        <input v-if="question.question_type === 'custom text only'" type="text"></input>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  props: ['quiz', 'tracker', 'showQuiz'],
  methods: {
    send() {
      alert('Show quiz')
    }
  }
}
</script>

<style>
  .quiz {
    position: absolute;
    display: block;
    top: 3rem;
    left: 0;
    right: 0;
    bottom: 3rem;
    background: #fff;
    z-index:1000;
    overflow-x: hidden;
    overflow-y: scroll;
  }
  .quiz_visible {
    display: block;
  }

  .quiz > form {
    min-width:300px;
    margin:2rem 30vw 0;
  }
</style>



