<template>
  <div class="observation-list">
    <div v-if="observations.length">
      <button class="button__h3 light-blue" @click="loadObservations">Update list</button>
      <h3>Avaliable Observations</h3>

      <div class="observations" v-for="observation in observations">
        <observation-item v-bind:observation="observation"></observation-item>
      </div>
    </div>

    <div v-if="!observations.length">
      <p class="text-center caption">There are no observations on the device.</p>
      <button class="full-width big" @click="loadObservations">Load observations from server</button>
    </div>
  </div>
</template>

<style>
  .observation-list {
    margin:2rem 2rem;
  }

  .button__h3 {
    float: right;
    margin-top:0.6rem;
  }
</style>

<script>
  import ObservationItem from './ObservationItem'
  import db from '../helpers/db'
  import _ from 'lodash'

  export default {
    components: {
      ObservationItem
    },
    data () {
      return {
        isLoading: false
      }
    },
    created () {
      this.loadObservations();
    },
    computed: {
      observations () {
        return _.orderBy(this.$store.state.observations, 'planned_date', ['desc'])
      }
    },
    methods: {
      loadObservations () {
        this.isLoading = true
        db.observations(this.$store.state.auth_token).then((data) => {
          this.$store.dispatch('setObservations', data);
          this.isLoading = false
        }, () => {
          this.isLoading = false
        })
      }
    }
  }
</script>



