<template>
  <button v-if="isAuthorized" v-on:click.prevent="logout">Logout</button>
</template>

<script>
  import router from '../router/index'

  export default {
    computed: {
      isAuthorized: function(){
        return !!this.$store.state.auth_token
      }
    },
    methods: {
      logout (e) {
        this.$store.dispatch('clearAuthToken');
        this.goToLogin();
      },
      goToLogin () {
        router.push({ name: 'login' })
      }
    }
  }
</script>



