<template>
  <header class="toolbar">
    <slot name="left"></slot>
    <div class="toolbar-content"><div class="toolbar-title">{{title}}</div></div>
    <slot name="right"></slot>
  </header>
</template>

<style>
  .toolbar {
    padding: 4px;
    height: 3rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index:9999;
    color: #424242;
    background: #f4f4f4;
    border-bottom:1px solid #e0e0e0;
  }
  .toolbar-content {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    font-weight: 600;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 1.2rem;
    text-align: center;
  }
  .toolbar-title {
     pointer-events: none;
     position: absolute;
     top: 0;
     left: 0;
     padding: 0 90px 1px;
     width: 100%;
     height: 100%;
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     -webkit-box-align: center;
     -ms-flex-align: center;
     align-items: center;
     -webkit-box-pack: center;
     -ms-flex-pack: center;
     justify-content: center;
   }
  .toolbar button {
    margin: 0 .2rem;
  }
</style>

<script>
  import ButtonLogout from './ButtonLogout'
  export default {
    props: ['title'],
    components: {
      ButtonLogout
    }
  }
</script>



