<template>
  <div class="map" id="map"></div>
</template>

<script>
import micello from '../scripts-map/micellomap'
import {tMapsTheme } from '../scripts-map/map-theme'

var map;

export default {
  props: ['tracker'],
  mounted: function(){
  },
  unmounted: function(){
    map.unload();
  },
  watch: {
    tracker (value) {
      map = new micello();
      map.setMapName('map');
      map.setMapTheme(tMapsTheme);
      map.setMapData(this.$store.state.observation.map);
      map.setMapActivities(this.$store.state.observation.activities);
      map.setTracker(this.tracker);
      map.load();
    }
  }
}
</script>

<style>
  .map {
    width:100vw;
    height:calc(100vh - 6rem);
  }
</style>



