<template>
  <button v-on:click.prevent="onClick">{{title}}</button>
</template>
<script>
export default {
  props: ['click'],
  data () {
    return {
      title: 'Show Quiz'
    }
  },
  methods: {
    onClick () {
      if(typeof this.click === 'function'){
        this.click();
      }
      this.title = this.title === 'Show Quiz' ? 'Hide Quiz' : 'Show Quiz'
    }
  }
}
</script>



