<template>
  <div class="observation-screen">
    <app-header v-bind:title="title">
      <button-observation-back slot="left"></button-observation-back>
      <button-observation-quiz v-bind:click="toggleQuiz" slot="right"></button-observation-quiz>
    </app-header>
    <pre style="position: absolute; top: 3rem;right: 0;bottom: 3rem;overflow-y:auto;width: 30%;z-index: 100; background:rgba(255,255,255,.7);">{{tracker}}</pre>
    <map-view v-bind:tracker="tracker"></map-view>
    <quiz v-bind:tracker="tracker" v-bind:quiz="quiz" v-bind:show-quiz="showQuiz"></quiz>
    <footer class="footer">
      <div class="footer__button">
        <button-observation-undo></button-observation-undo>
      </div>
      <div class="footer__button">
        <button-observation-reset></button-observation-reset>
      </div>
      <div class="footer__button footer__button_full">
        <button-observation-complete></button-observation-complete>
      </div>
    </footer>
  </div>
</template>

<script>
import Tracker from '../helpers/tracker'
import MapView from '../components/MapView'
import Quiz from '../components/Quiz'
import AppHeader from '../components/AppHeader'
import ButtonObservationBack from '../components/ButtonObservationBack'
import ButtonObservationReset from '../components/ButtonObservationReset'
import ButtonObservationUndo from '../components/ButtonObservationUndo'
import ButtonObservationQuiz from '../components/ButtonObservationQuiz'
import ButtonObservationComplete from '../components/ButtonObservationComplete'

export default {
  components: {
    AppHeader,
    MapView,
    Quiz,
    ButtonObservationBack,
    ButtonObservationReset,
    ButtonObservationUndo,
    ButtonObservationQuiz,
    ButtonObservationComplete
  },
  data (){
    return {
      tracker: '',
      title: '',
      showQuiz: false,
      quiz: ''
    }
  },
  mounted () {
    this.tracker = new Tracker(this.$store.state.tracker_id, this.$store.state.observation.id);
    this.title = this.$store.state.observation.name;
    // init shopper
    // save shopper
    // clear shopper
  },
  computed: {
    quiz: function() {
      return this.$store.state.observation.quiz;
    }
  },
  methods: {
    toggleQuiz () {
      this.showQuiz = !this.showQuiz
    }
  }
}
</script>

<style>
  .footer {
    height:3rem;
    padding:4px;
    background: #f4f4f4;
    border-top:1px solid #e0e0e0;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: stretch;
    align-items: flex-start;
  }

  .footer__button {
    order: 0;
    flex: 1 0 auto;
    align-self: auto;
    padding-right:1rem;
  }
  .footer__button_full {
    flex: 1 1 100%;
    padding-left:10rem;
    padding-right:0rem;
  }
</style>



