<template>
  <div id="login" class="login">
    <form class="form" v-on:submit.prevent="submitForm" name="loginForm">
      <error-list v-bind:errors="errors"></error-list>
      <div class="clearfix">
        <label for="username">Email</label>
        <input type="email" name="username" id="username" value="" placeholder="Login" v-model="username" required>
      </div>
      <div class="clearfix">
        <label for="password">Password</label>
        <input name="password" id="password" type="password" value="" placeholder="Password" v-model="password" required>
      </div>
      <div class="clearfix">
        <!-- label>&nbsp;<span ng-hide="connectionStatus">You need&nbsp;<i class="icon-signal"></i></span></label -->
        <button type="submit" v-bind:disabled="isValidForm">Login</button>
      </div><!--.clearfix
            <label class="third right">&nbsp;</label>
            <input type="butx`ton" value="Reset Data" ng-click="resetData()" class="button danger half">-->
    </form>
  </div>
</template>

<script>
  import AppHeader from './AppHeader'
  import ErrorList from './ErrorList'
  import router from '../router/index'
  import db from '../helpers/db'

  export default {
    components: {
      AppHeader,
      ErrorList
    },
    data: function(){
      return {
        username: 'tracker1@user.com',
        password: '12341234',
        errors: []
      }
    },
    computed: {
      isValidForm: function(){
        return !(!!this.username && !!this.password)
      }
    },
    created: function(){
      if (this.$store.state.auth_token){
        this.goToLlist();
      }
    },
    methods: {
      submitForm: function(e){
        db.login(this.username, this.password).then((data) => {
          this.$store.dispatch('setAuth', data);
          this.goToLlist();
        }, (data) => {
          this.errors = data.body.errors;
        })
      },
      goToLlist: function(){
        router.push({ name: 'list' })
      }
    }
  }
</script>

<style>
  .login {
    min-width:300px;
    margin:20vh 30vw 0;
  }
  h1 {
    text-align: center;
  }
</style>



